/**
 * @namespace dateTool
 */
const dateTool = {
  /**
   * 
   * @memberOf dateTool
   * @function time2str
   * @param {date} date - 
   * @param {string} type - 
   * e.g. "date-time" => 2016-5-31 10:25:10
   * e.g. "date" => 2016-5-31
   * e.g. "month-day" => 5-31
   * e.g. "time" => 10:30
   * e.g. "china-date" => 2016531
   * e.g. "week" => 531
   * @returns {string} 
   */
  time2str: function (date, type) {
    const _date = date;
    if (typeof _date === "number") date = new Date(_date);

    var weekdayMap = {
      1: "",
      2: "",
      3: "",
      4: "",
      5: "",
      6: "",
      0: "",
    };

    var weekday = weekdayMap[date.getDay()];
    var day = date.getDate().toString();
    if (day.length === 1) day = "0" + day;
    var mou = (date.getMonth() + 1).toString();
    if (mou.length === 1) mou = "0" + mou;
    var yea = date.getFullYear().toString();
    var hou = date.getHours().toString();
    if (hou.length === 1) hou = "0" + hou;
    var min = date.getMinutes().toString();
    if (min.length === 1) min = "0" + min;
    var sen = date.getSeconds().toString();
    if (sen.length === 1) sen = "0" + sen;

    if (type === "date-time") {
      return yea + "-" + mou + "-" + day + " " + hou + ":" + min + ":" + sen;
    } else if (type === "date") {
      return yea + "-" + mou + "-" + day;
    } else if (type === "month-day") {
      return mou + "-" + day;
    } else if (type === "time") {
      return hou + ":" + min;
    } else if (type === "china-date") {
      return yea + "" + mou + "" + day + "";
    } else if (type === "week") {
      return weekday + "(" + mou + "" + day + ")";
    }
  },

  /**
   * 
   * @memberOf dateTool
   * @function str2time
   * @param {string} strTime - 
   * e.g. "2017-02-13 10:02:58"
   * e.g. "2017-02-13"
   * e.g. "9:10"
   */
  str2time: function (strTime) {
    // new Date(year, month[, day[, hour[, minutes[, seconds[, milliseconds]]]]]);
    strTime = strTime.trim();
    var _type;
    if ((strTime.indexOf("-") > -1) && (strTime.indexOf(":") > -1)) {
      _type = "dateTime";
    } else if ((strTime.indexOf("-") > -1) && (strTime.indexOf(":") < 0)) {
      _type = "date";
    } else if ((strTime.indexOf("-") < 0) && (strTime.indexOf(":") > -1)) {
      _type = "time";
    }

    var arr;
    var date;
    var time;
    var dateArr;
    var year;
    var month;
    var day;
    var timeArr;
    var hour;
    var minutes;
    var seconds;

    if (_type === "dateTime") {
      arr = strTime.split(" ");

      date = arr[0];
      dateArr = date.split("-");
      year = dateArr[0];
      month = dateArr[1];
      day = dateArr[2];

      time = arr[1];
      timeArr = time.split(":");
      hour = timeArr[0];
      minutes = timeArr[1];
      seconds = timeArr.length > 2 ? timeArr[2] : 0;

      return new Date(year, month - 1, day, hour, minutes, seconds);
    } else if (_type === "date") {
      date = strTime;
      dateArr = date.split("-");
      year = dateArr[0];
      month = dateArr[1];
      day = dateArr[2];

      return new Date(year, month - 1, day);
    } else if (_type === "time") {
      date = new Date();
      year = date.getFullYear();
      month = date.getMonth();
      day = date.getDate();

      time = strTime;
      timeArr = time.split(":");
      hour = timeArr[0];
      minutes = timeArr[1];
      seconds = timeArr.length > 2 ? timeArr[2] : 0;

      return new Date(year, month - 1, day, hour, minutes, seconds);
    }
  }
}

export default dateTool;