/**
 * @namespace fileTool
 */
const fileTool = {
  /**
   * base64
   * @memberof fileTool
   * @param {image} img
   * @returns {promise}
  */
  getImgBase64: function (img) {
    return new Promise(resolve => {
      const reader = new FileReader();
      reader.addEventListener('load', () => {
        resolve(reader.result);
      });
      reader.readAsDataURL(img);
    });
  }
};
export default fileTool;