/**
 * @namespace variables
*/

const variables = {
  /**
   * object
   * @memberOf variables
   * @function isObject
   * @param {*} obj 
  */
  isObject: function (obj) {
    return Object.prototype.toString.call(obj) === "[object Object]";
  },

  /**
   * object
   * @memberOf variables
   * @function isEmptyObject
   * @param {object} obj
  */
  isEmptyObject: function (obj) {
    for (var key in obj) {
      return false;
    }
    return true;
  },

  /**
   * Promise
   * @memberOf variables
   * @param {function} fn - 
  */
  isPromise: function (fn) {
    return this.isCorrect(fn) && typeof fn.then === "function";
  },

  /**
   * 
   * @memberOf variables
   * @function isCorrect
   * @param {*} variable - 
   * @param {boolean} [notBezero=false] - 
  */
  isCorrect: function (variable, notBezero) {
    var result = true;
    if (
      (typeof variable === "string") &&
      (
        variable === "" ||
        variable === "undefined" ||
        variable === "null" ||
        variable === "NaN" ||
        variable === "Infinity"
      )
    ) {
      result = false;
    } else if (
      (typeof variable === "number") &&
      (
        isNaN(variable) ||
        !isFinite(variable) ||
        (notBezero && variable === 0)
      )
    ) {
      result = false;
    } else if (variable === null) {
      result = false;
    } else if (typeof variable === "undefined") {
      result = false;
    } else if (this.isObject(variable)) {
      if (this.isEmptyObject(variable)) {
        result = false;
      }
    } else if (Array.isArray(variable)) {
      if (variable.length === 0) {
        result = false;
      }
    }
    return result;
  },

  /**
   * array object 
   * @memberOf variables
   * @function cloneDeep
   * @param {object|array} target - 
  */
  cloneDeep: function (target) {
    var isObject = this.isObject(target);
    var isArray = Array.isArray(target);

    if (isObject || isArray) {
      var newObj = {};
      for (var key in target) {
        newObj[key] = this.cloneDeep(target[key]);
      }
      return newObj;
    }

    return target;
  }
}
export default variables;